<template>
    <div style="padding:0 30px">
        <div style="margin-bottom: 30px">
            <el-button type="primary"  @click="explorTable" ></el-button>
        </div>
        <el-table :data="signupData.results" :fit="true">
            <el-table-column property="name" label="" width="100px"></el-table-column>
            <el-table-column property="student_id" label="" width="130px"></el-table-column>
            <el-table-column property="profession" label=""></el-table-column>
            <el-table-column property="contact" label=""></el-table-column>
<!--            <el-table-column property="is_activate" label="" width="100px"></el-table-column>-->
            <el-table-column label="" width="100px">
                <template slot-scope="scope">
                    <div style="font-size: 22px" :class="scope.row.is_activate?'el-icon-circle-check cu-green':'el-icon-circle-close cu-blue'">

                    </div>
                </template>
            </el-table-column>
            <el-table-column property="date" :formatter="formatDate" label=""></el-table-column>
        </el-table>
        <div class="" style="text-align: center;position: fixed;bottom: 50px;width: 50%">
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :page-count="signupData.num_pages"
                    @current-change="handPageClick">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: "signuptable",
        props:{
          id:null,
        },
        data(){
            return{
                signupData:{
                    results:null
                },
            }
        },
        created() {
            console.log("");
            this.getSignUpData();
        },
        methods:{
            explorTable(){
                this.$api.explorTableData(this.id).then(res=>{
                    if (!res) {
                        return
                    }
                    let url = window.URL.createObjectURL(new Blob([res.data]));
                    let link = document.createElement('a');
                    link.style.display = 'none';
                    link.href = url;
                    link.setAttribute('download', '.xls');
                    document.body.appendChild(link);
                    link.click();
                    window.URL.revokeObjectURL(url);
                    document.body.removeChild(link);
                })
            },
            handPageClick(e){
                this.getReserverData(e);
            },
            formatDate(row, column, cellValue, index){

                return this.$moment(cellValue).format("YYYY-MM-DD HH:mm")
            },
            getSignUpData(){
                this.$api.getSignUpData(this.id).then(res=>{
                    if (res.status === 200) {
                        this.signupData = res.data;
                        console.log(res.data)
                    }
                })
            }
        },
    }
</script>

