/**
 * api
 */

import base from './baseUrl';
import axios from '@/utils/http';
import qs from 'qs';
import el from "element-ui/src/locale/lang/el";

const api = {
    login (data) {
        return axios.post(`${base.admin}/login/`, qs.stringify(data));
    },
    getGloabdata(){
        return axios.get(`${base.admin}/getalldata/`)
    },
    reserverReview(data){
        return axios.post(`${base.admin}/review/`, qs.stringify(data,{ arrayFormat: 'repeat'}));
    },
    deleteManyRoomData(data){
        return axios.post(`${base.admin}/room/deletemany/`, qs.stringify(data,{ arrayFormat: 'repeat'}));
    },
    getSignUpData(params){
        return axios.get(`${base.admin}/signup/${params}`);
    },
    explorTableData(params){
        return axios.get(`${base.admin}/signup/${params}/getdata/`, { responseType: 'blob' });
    },
    CollegeData(method,params=null,value=null){
        var url = null;
        if(method==='get'){
            url = `${base.admin}/college/`
        }else{
            url = `${base.admin}/college/${params}/`
        }
        return axios({
            method:method,
            url:url,
            data:qs.stringify(value),
        })
    },
    getReserverData(page){
        if(page===null){
            return axios.get(`${base.admin}/reserver/`)
        }else {
            return axios.get(`${base.admin}/reserver/?page=${page}`)
        }
    },
    getRequestData(){
        return axios.get(`${base.admin}/request/`)
    }
};

export default api;