<template>
    <div class="cu-top">
        <el-card shadow="always" style="height: 100%" >
            <el-row type="flex" class="" style="height: 100%">
                <el-col :span="16">
                    <el-row type="flex" style="height: 50%">
                        <el-col :span="24">
                            <div id="servenId" style="height: 300px;width: 100%" @scroll.passive="true" ></div>
                        </el-col>
                    </el-row>
                    <el-row type="flex" style="height: 50%">
                        <el-col :span="24">
                            <div id="LineDetailId" style="height: 300px" @mousewheel.passive="false"></div>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="8" class="pie-position">
                    <div id="pieID" style="height: 340px;width: 100%"></div>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
    import echarts from 'echarts/lib/echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/chart/bar'
    import 'echarts/lib/component/legend'
    import 'echarts/lib/component/legendScroll'
    import 'echarts/lib/component/title'
    import 'echarts/lib/component/markLine'
    import 'echarts/lib/component/markPoint'
    import 'echarts/lib/component/tooltip'
    import 'echarts/theme/macarons';

    import {option_line, option_pie,option_line_detail} from '../data/statistics'
    // import hallow from '../data/hallow'

    export default {
        name: "Statistics",
        data() {
            return {}
        },
        created() {

        },
        mounted() {
            this.$api.getRequestData().then(res => {
                if (res.status === 200) {
                    this.SortOutData(res.data);
                    this.initCharts();
                }
            });
        },
        methods: {
            SortOutData(data) {
                // ID option_line
                var [date, get, post, del] = [[], [], [], []];
                for (var x of data.results.date) {
                    date.push(x.date);
                    get.push(x.get);
                    post.push(x.post);
                    del.push(x.delete)
                }
                option_line.xAxis.data = date;
                var tmpdata = [get, post, del];
                tmpdata.forEach((item, index) => {
                    option_line.series[index].data = item
                });
                // ID option_pie
                option_pie.series[0].data[0].value = data.allGet;
                option_pie.series[0].data[1].value = data.allPost;
                option_pie.series[0].data[2].value = data.allDel;
                option_pie.title[1].subtext = String(data.allGet+data.allPost+data.allDel)+'';
                // ID option_line_detail
                var timeKeys = Object.keys(data.results.time);
                var valueKeys = Object.values(data.results.time);
                option_line_detail.xAxis.data = timeKeys;
                option_line_detail.series[0].data = valueKeys;
                var allCount = valueKeys.reduce((x,y)=>{
                    return x+y
                });
                option_line_detail.series[1].data = [];
                valueKeys.forEach(item=>{
                    option_line_detail.series[1].data.push(Math.ceil(item/allCount*100));
                })

            },
            initCharts() {
                echarts.init(document.getElementById("servenId"), 'macarons').setOption(option_line);
                echarts.init(document.getElementById("pieID"), 'macarons').setOption(option_pie);
                echarts.init(document.getElementById("LineDetailId"), 'macarons').setOption(option_line_detail);
            },
        }

    }
</script>

<style scoped>
    .pie-position{
        display: flex;
        align-items: center;
        /*padding-bottom: 15%*/
    }

    .el-card >>> .el-card__body{
        height: 100%;
    }
</style>