<template>
    <div class="cu-top">
        <el-card shadow="always">
            <div class="coll-content">
                <el-form label-position="left" label-width="80px" :model="formLabelAlign" ref="formLabelAlign" :disabled="!canEdit">
                    <el-form-item label="" prop="college_name" :rules="[{required: true, message: '', trigger: 'blur' }]" :error="errformLabelAlign.college_name">
                        <el-input v-model="formLabelAlign.college_name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="content" :error="errformLabelAlign.content">
                        <tinymce v-model="formLabelAlign.college_intro" :height="300"></tinymce>
                    </el-form-item>
                </el-form>
            </div>
            <el-row :gutter="20" class="deta-row">
                <el-col :span="12">
                    <div class="" style="text-align: left">
                        <el-button icon="el-icon-edit" type="warning" @click="canEdit=true"></el-button>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="" style="text-align: right" v-if="canEdit">
                        <el-button icon="el-icon-edit" type="primary" @click="getCollegeData('put')"></el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
    import Tinymce from './Tinymce'


    export default {
        name: 'College',
        components: {
            Tinymce
        },
        data() {
            return {
                formLabelAlign: {
                    college_name: '',
                    college_intro: '',
                },
                errformLabelAlign:{
                    college_name: '',
                    college_intro: '',
                },
                canEdit:false,

            }
        },
        created() {
            this.getCollegeData('get');
        },
        methods:{
            getCollegeData(method){
                if(method==='put'){
                    var resultsValid = false;
                    this.$refs['formLabelAlign'].validate((valid) => {
                            resultsValid = valid
                        }
                    );
                    if (!resultsValid) {
                        return
                    }
                }
                if(method==='get'){
                    this.$api.CollegeData(method).then(
                        res=>{
                            if(res.status===200){
                                this.formLabelAlign = res.data;
                            }
                        }
                    )
                }else if(method==='put'){
                    this.$api.CollegeData(method,this.formLabelAlign.id,this.formLabelAlign).then(
                        res=>{
                            this.canEdit = false;
                            if(res.status===200){
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                            }
                        }
                    )
                }

            }
        }
    }
</script>

<style scoped>

    .coll-content {
        margin-right: 500px;
        min-height: 200px;
    }

    .deta-row {
        min-height: 90px;
        background: #f8f8f8;
        padding: 30px;
        border-radius: 4px;
        border: 1px solid #eeeeee;
    }

    .coll-edit {
        text-align: left;
        line-height: initial;
    }

    .coll-edit >>> .ql-editor {
        height: 300px;
    }
</style>

