<template>
    <div class="cu-body">
        <el-container>
            <el-aside width="null">
                <el-menu :default-active="DetaultActive"
                         class="el-menu-vertical-demo"
                         :router="true"
                         :collapse="isCollapse">
                    <template v-for="(item,index) in menuData">
                        <el-menu-item :index="item.index" v-if="item.children.length===0">
                            <i class="el-icon-setting"></i>
                            <span slot="title">{{item.name}}</span>
                        </el-menu-item>
                        <el-submenu :index="item.index" v-else>
                            <template slot="title">
                                <i class="el-icon-location"></i>
                                <span slot="title">{{item.name}}</span>
                            </template>
                            <el-menu-item :index="chitem.index"
                                          v-for="(chitem,index) in item.children" :key="index">{{chitem.name}}
                            </el-menu-item>
                        </el-submenu>
                    </template>
                </el-menu>
            </el-aside>
            <el-container>
                <el-header height="null">
                    <div class="cu-header-top">
                        <div class="cu-header">
                            <div class="cu-collapse" :class="isCollapse?'el-icon-s-grid':'el-icon-menu'"
                                 @click="CollapseOn"></div>
                            <el-breadcrumb separator="/">
                                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                                <el-breadcrumb-item v-for="(item,index) in nowPath" :key="index" v-if="item.name!==''"
                                                    :to="null">{{item.name}}
                                </el-breadcrumb-item>
                            </el-breadcrumb>
                        </div>
                        <div class="cu-avatar">
                            <el-dropdown trigger="click" @command="handDropDown">
                              <span class="el-dropdown-link">
                                  <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
                                             shape="square"></el-avatar>
                              </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item icon="el-icon-plus" :command="getLoginOut"></el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-circle-plus"></el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-circle-plus-outline"></el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-check"></el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-circle-check"></el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                            <div class="cu-avatar-name"></div>
                        </div>
                    </div>
                    <div class="cu-header-bottom">
                        <el-tabs v-model="editableTabsValue" type="border-card" @edit="handleTabsEdit"
                                 @tab-click="handTableClick" class="cu-tables">
                            <el-tab-pane :closable="item.closable" :key="item.name"
                                         v-for="(item, index) in editableTabs"
                                         :name="item.name"
                                         :label="item.title">
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                </el-header>
                <el-main style="position: relative">
                    <transition name="custom-classes-transition"
                                enter-active-class="animated slideInRight"
                                leave-active-class="animated slideOutLeft">
                        <keep-alive :exclude="['RoomDetail']">
                            <router-view>

                            </router-view>
                        </keep-alive>
                    </transition>
                </el-main>
            </el-container>
        </el-container>
    </div>

</template>

<script>
    import menuData from "../data/menu";

    export default {
        name: "Home",
        data() {
            return {
                isCollapse: false,
                menuData: menuData,
                nowPath: [],
                DetaultActive: '/gloabboard',
                editableTabsValue: 'gloabboard',
                editableTabs: [{
                    title: '',
                    route: {name: 'home'},
                    name: 'gloabboard',
                    closable: false,
                }],
            }
        },
        created() {

        },
        methods: {
            CollapseOn() {
                this.isCollapse = this.isCollapse !== true;
            },
            handleTabsEdit(targetName, action) {
                console.log(targetName, action);
                if (action === 'remove') {
                    let tabs = this.editableTabs;
                    let activeName = this.editableTabsValue;
                    if (activeName === targetName) {
                        tabs.forEach((tab, index) => {
                            if (tab.name === targetName) {
                                let nextTab = tabs[index + 1] || tabs[index - 1];
                                if (nextTab) {
                                    activeName = nextTab.name;
                                }
                            }
                        });
                    }
                    this.editableTabsValue = activeName;
                    this.editableTabs = tabs.filter(tab => tab.name !== targetName);
                }
            },
            handTableClick(e) {
                this.$router.push(this.editableTabs[parseInt(e.index)].route);
            },
            handDropDown(e){
                e()
            },
            getLoginOut(){
                localStorage.removeItem('token');
                this.$store.commit('setToken', {'token': null});
                this.$router.push({name:'login'});
            },
        },
        watch: {
            $route(to, from) {
                if (to.meta.menu === true) {
                    this.DetaultActive = to.path;
                    this.nowPath = [];
                    this.nowPath.push({name: to.meta.parent}, {name: to.meta.detailname});
                    var haveTable = this.editableTabs.findIndex(item => {
                        return item.title === to.meta.detailname
                    });
                    if (haveTable === -1) {
                        this.editableTabs.push({
                            title: to.meta.detailname,
                            route: {name: to.name},
                            name: to.name,
                            closable: true
                        });
                    }
                    this.editableTabsValue = to.name;
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if (to.meta.menu === true) {
                    if (to.path === '/') {
                        vm.DetaultActive = '/gloabboard';
                    } else {
                        vm.DetaultActive = to.path;
                    }
                    vm.nowPath = [];
                    vm.nowPath.push({name: to.meta.parent}, {name: to.meta.detailname});
                    if (to.name !== 'gloabboard') {
                        vm.editableTabs.push({
                            title: to.meta.detailname,
                            route: {name: to.name},
                            name: to.name,
                            closable: true
                        });
                        vm.editableTabsValue = to.name;
                    }
                }
            })
        }
    }
</script>

<style scoped>
    @import "../assets/animate.css";
    .cu-body {
        width: 100%;
        height: 100vh;
        background: #ffffff;
    }

    .cu-body > .el-container {
        height: 100%;
    }

    .el-menu {
        height: 100%;
        text-align: left;

    }

    .el-header {
        padding: 0;
        display: flex;
        /*flex-direction: column;*/
        /*align-items: center;*/
        /*justify-content: space-between;*/
        flex-direction: column;

    }


    .cu-header {
        display: flex;
    }

    .cu-header-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;

    }

    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }

    .cu-collapse:hover {
        color: #409EFF;
    }

    .cu-collapse {
        margin-right: 20px;
        font-size: 18px;
    }

    .cu-avatar {
        display: flex;
        align-items: center;
    }

    .cu-avatar-name {
        max-width: 60px;
        max-height: 60px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
    }

    .el-tabs--border-card > .el-tabs__content {
        padding: 0;
    }

    .el-main {
        padding: 0;
    }

    .el-tabs {
        box-shadow: initial;
        border-left: 0;
        /*height: 100%;*/
        box-sizing: border-box;
        border-bottom: 0;
    }

    .el-tabs >>> .el-tabs__content {
        padding: 0;
    }

    .fold-left-enter-active {
        animation-name: fold-left-in;
        animation-duration: .3s;
    }

    .fold-left-leave-active {
        animation-name: fold-left-out;
        animation-duration: .3s;
    }

    @keyframes fold-left-in {
        0% {
            -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
            /* visibility: visible; */
        }
        /*50% {
          transform: translate3d(50%, 0, 0);
        }*/
        100% {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }

    @keyframes fold-left-out {
        0% {
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
        /*50% {
          transform: translate3d(-50%, 0 , 0);
        }*/
        100% {
            -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
            /* visibility: hidden; */
        }
    }

    .fold-right-enter-active {
        animation-name: fold-right-in;
        animation-duration: .3s;
    }

    .fold-right-leave-active {
        animation-name: fold-right-out;
        animation-duration: .3s;
    }

    @keyframes fold-right-in {
        0% {
            width: 100%;
            -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
            /* visibility: visible; */
        }
        /*50% {
          transform: translate3d(50%, 0, 0);
        }*/
        100% {
            width: 100%;
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
    }

    @keyframes fold-right-out {
        0% {
            width: 100%;
            -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
        }
        /*50% {
          transform: translate3d(-50%, 0 , 0);
        }*/
        100% {
            width: 100%;
            -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
            /* visibility: hidden; */
        }
    }
</style>