export var option_pie = {
    title: [
        {
            text: '',
            textStyle: {
                fontSize: 16,
                color: "#3498db"
            },
            left: "35%",
            bottom: "0%",
        },
        {
            text: '',
            subtext: 0+'',
            textStyle:{
                fontSize:20,
                color:"#9b59b6"
            },
            subtextStyle: {
                fontSize: 20,
                color: '#9b59b6'
            },
            textAlign:"center",
            x: '34.5%',
            y: '40%',
        }],
    tooltip: {
        trigger: 'item',
        formatter:function (parms){
            return  parms.seriesName + "</br>" +
                parms.marker + "" + parms.data.legendname + "</br>" +
                "" + parms.data.value + "</br>" +
                "" + parms.percent + "%" ;
        }
    },
    legend: {
        type:"scroll",
        orient: 'vertical',
        left:'70%',
        align:'left',
        top:'middle',
        textStyle: {
            color:'#8C8C8C'
        },
        height:250
    },
    series: [
        {
            name:'',
            type:'pie',
            center: ['35%', '50%'],
            radius: ['40%', '80%'],
            clockwise: false, //
            avoidLabelOverlap: true,
            label: {
                normal: {
                    show: true,
                    position: 'outter',
                    formatter:function (parms){
                        return parms.data.legendname
                    }
                }
            },
            labelLine: {
                normal: {
                    length:18,
                    length2:22,
                    smooth:true,
                }
            },
            data:m2R2Data
        }
    ]
};


