<template>
    <div class="cu-top">
        <el-card shadow="always">
            <div class="ro-left">
                <el-button type="primary" icon="el-icon-plus" @click="goAdd"></el-button>
                <el-button type="danger" icon="el-icon-delete" @click="goDelete"></el-button>
            </div>
            <el-table
                    ref="multipleTable"
                    :data="allData.results"
                    @selection-change="getSelectDatas"
                    tooltip-effect="dark"
                    :fit="true"
                    stripe
                    style="width: 100%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        label="">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">
                            <router-link :to="{path:'/room/'+scope.row.id}"  style="text-decoration: none;">
                                <el-link :underline="false" type="primary">{{ scope.row.room_name}}</el-link>
                            </router-link>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="room_introduction"
                        label="">
                </el-table-column>
            </el-table>
            <div class="ro-pagination">
                <el-pagination
                        background
                        layout="prev, pager, next"
                        :page-count="allData.num_pages"
                        @current-change="handPageClick">
                </el-pagination>
            </div>
        </el-card>
    </div>
</template>

<script>
    import base from '@/utils/baseUrl';
    import qs from 'qs';


    // import {EventBus}  from "@/assets/event-bus";

    export default {
        name: "Room",
        data() {
            return {
                allData:{results:null},
                idList:null,
            }
        },
        created() {

        },
        activated(){
            this.getRoomData()
        },
        methods: {
            getRoomData(){
                this.$http({
                    method: 'get',
                    url: `${base.admin}/room/`,
                }).then(res=>{
                    console.log(res.data);
                    this.allData = res.data;
                })
            },
            handPageClick(e){
                this.$http({
                    method: 'get',
                    url: `${base.admin}/room/?page=${e}`,
                }).then(res=>{
                    console.log(res.data);
                    this.allData = res.data;
                })
            },
            goAdd(){
                this.$router.push({name:'roomdetail', params:{id:'add'}})
            },
            getSelectDatas(selections){
                this.idList = [];
                for(var item of selections){
                    this.idList.push(item.id)
                }
                console.log(selections);
            },
            goDelete(){
                console.log(this.idList);
                var data = {
                    type:'delete',
                    idlist:this.idList
                };
                this.$api.deleteManyRoomData(data).then(res=>{
                    if (res.status===200){
                        if(res.data.status==='success'){
                            for(let item in this.idList){
                                let index = this.allData.results.findIndex(res=>{
                                   return res.id===item
                                });
                                if(index!==-1){
                                    this.allData.results.splice(index,1)
                                }
                            }
                            this.$message({
                                message: '',
                                type: 'success',
                                duration:1500,
                            });
                        }
                    }
                }).catch();
            }
        },
    }
</script>

<style scoped>

    .ro-left {
        text-align: left;
        padding-bottom: 20px;
    }

    .ro-pagination{
        margin-top: 50px;
    }
</style>