<template>
    <div>
        <el-container>
            <el-main>
                <el-row type="flex" class="row-bg" justify="space-around">
                    <el-col :span="6">
                        <div class="grid-content">
                            <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
                                     class="demo-ruleForm">
                                <el-form-item>
                                    <div class="cu-title">
                                        
                                    </div>
                                </el-form-item>
                                <el-form-item prop="username">
                                    <el-input prefix-icon="el-icon-user" v-model="ruleForm.username"></el-input>
                                </el-form-item>
                                <el-form-item prop="pass">
                                    <el-input type="password" prefix-icon="el-icon-lock" v-model="ruleForm.pass"
                                              :show-password="true"></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                                    <el-button @click="resetForm('ruleForm')"></el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                ruleForm: {
                    username: '',
                    pass: '',
                },
                rules: {
                    username: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 3, max: 255, message: '3', trigger: 'blur'}
                    ],
                    pass: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                }
            };
        },
        created(){
            if(this.$store.state.token){
                this.$router.replace({name: 'home'});
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$api.login({
                            username: this.ruleForm.username,
                            password: this.ruleForm.pass
                        }).then((res) => {
                            console.log(res);
                            var token = JSON.stringify(res.data);
                            this.$store.commit('setToken', {'token': token});
                            localStorage.setItem('token', token);
                            this.$router.replace({name: 'home'});
                        }).catch(function (error) {
                            console.log(error);
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>
    body {
        background: url("https://api.paugram.com/wallpaper/?source=sina&category=jp");
    }

    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
    }

    .el-main {
        background-color: #2d3a4b;
        /*background: url("https://s0.xinger.ink/acgimg/acgurl.php");*/
        color: #333;
        text-align: center;
        height: 100vh;
        padding: 0;
    }

    .el-container:nth-child(5) .el-aside,
    .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }

    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }

    .el-row {
        &:last-child {
            margin-bottom: 0;
        }
    }

    .el-col {
        border-radius: 4px;
    }

    .grid-content {
        border-radius: 4px;
        padding-top: 180px;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .cu-title {
        font-size: 28px;
        color: #ffffff;
        text-align: center;
        font-weight: 700;
    }

    .el-input >>> input {
        border: 1px solid hsla(0, 0%, 100%, .1);
        background: rgba(0, 0, 0, .1);
        border-radius: 5px;
        height: 48px;
        color: #ffffff;
        font-size: 16px;
        padding: 0 40px;

    }

    .el-input {
        font-size: 16px;
    }


</style>