<template>
    <div class="cu-top">
        <el-card shadow="always">
            <el-table
                    :data="tableData"
                    stripe
                    style="width: 100%">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div style="display: flex;flex-direction: column;">
                            <div style="padding-left: 10px;color:#409eff;font-weight: bold;cursor:pointer"  @click="OpenDrawer(scope.row.id)">
                                {{ scope.row.name }}
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="event_intro"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="belong"
                        label="">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div style="font-size: 22px" :class="scope.row.roomData.whether_used?'el-icon-circle-check cu-green':'el-icon-circle-close cu-blue'">

                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div style="display: flex;flex-direction: column;">
                            <div>
                                {{ $moment(scope.row.date,"YYYY-MM-DD").format("MDD")}}
                            </div>
                            <div>
                                {{ scope.row.start_time}}-{{scope.row.end_time}}
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">
                            {{scope.row.roomData.room_name}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div style="margin-left: 10px;display: inline-block">
                            <div style="display: flex;flex-direction: column;text-align: center">
                                <el-avatar :src="scope.row.userinfo.avatarUrl"></el-avatar>
                                <span>{{ scope.row.userinfo.nickName}}</span>
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        width="150">
                    <template slot-scope="scope">
                        <i :class="scope.row.contact_method | filterIco"></i>
                        <span style="margin-left: 10px">{{scope.row.contact_info}}</span>
                    </template>
                </el-table-column>
            </el-table>
            <div class="ro-pagination">
                <el-pagination
                        background
                        layout="prev, pager, next"
                        :page-count="tmpAllData.num_pages"
                        @current-change="handPageClick">
                </el-pagination>
            </div>
        </el-card>
        <el-drawer
                title=""
                :visible.sync="drawer"
                direction="rtl"
                size="50%">
            <signup-table v-if="drawer" :id="reserver_id"></signup-table>
        </el-drawer>
    </div>
</template>

<script>
    import signuptable from "./component/signuptable";
    export default {
        name: "Reserver",
        components:{
           "signup-table":signuptable
        },
        data() {
            return {
                reserver_id:null,
                drawer:false,
                tmpAllData:{num_pages:0},
                tableData: null,
            }
        },
        created() {
            this.getReserverData();
            this.$refs
        },
        methods: {
            getReserverData(page=null) {
                this.$api.getReserverData(page).then(res => {
                    if (res.status === 200) {
                        this.tableData = res.data.results;
                        this.tmpAllData = res.data;
                    }
                })
            },
            handPageClick(e){
                this.getReserverData(e);
            },
            OpenDrawer(id){
                this.reserver_id = id;
                this.drawer = true;
            },
            caonima(){

            }
        },
        filters: {
            filterIco(value) {
                if (value === 'qq') {
                    return 'iconfont icon-qq cu-blue'
                } else if (value === 'wx') {
                    return 'iconfont icon-weixin cu-green'
                } else {
                    return 'el-icon-mobile-phone cu-origin'
                }
            },
        }
    }
</script>

<style scoped>
    @import "../assets/cuico/iconfont.css";

    .ro-pagination{
        margin: 50px 0 20px;

    }

    .link-style{
        color: #409eff;
        text-decoration: none;
        font-weight: bold
    }
</style>